@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@300;400;500&display=swap");
* {
  padding: 0;
  margin: 0;
  list-style: none;
}

html {
  font-size: 62.5%;
  font-family: "Rubik", sans-serif;
  height: 100%;
}

body {
  height: 100%;
  display: flex;
  flex-direction: column;
  background-color: hsl(226deg, 43%, 10%);
}
@media screen and (min-width: 900px) {
  body {
    align-items: center;
    justify-content: center;
  }
}

main {
  flex-grow: 1;
  margin: 80px 25px;
}
@media screen and (min-width: 900px) {
  main {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 20px;
    grid-row-gap: 20px;
    margin: 0 150px;
  }
}
@media screen and (min-width: 1440px) {
  main {
    margin: 0 20vw;
  }
}

.informations {
  background-color: hsl(235deg, 46%, 20%);
  border-radius: 15px;
}
@media screen and (min-width: 900px) {
  .informations {
    grid-area: 1/1/3/2;
    display: flex;
    flex-direction: column;
  }
}
.informations .user {
  display: flex;
  align-items: center;
  color: white;
  background-color: hsl(246deg, 80%, 60%);
  border-radius: 15px;
  padding: 30px;
}
@media screen and (min-width: 900px) {
  .informations .user {
    flex-direction: column;
    align-items: flex-start;
    flex-grow: 1;
  }
  .informations .user img {
    margin-bottom: 30px;
  }
}
.informations .user img {
  height: 60px;
  border: solid white 3px;
  border-radius: 50%;
}
.informations .user h1 {
  margin-left: 30px;
  font-size: 1.5rem;
  font-weight: 300;
  line-height: 2.7rem;
}
.informations .user h1 span {
  font-size: 2.2rem;
  font-weight: 400;
}
@media screen and (min-width: 900px) {
  .informations .user h1 {
    margin: 0;
    line-height: 3rem;
  }
  .informations .user h1 span {
    font-size: 3.5rem;
    font-weight: 300;
    line-height: 4rem;
  }
}
.informations .periodicity {
  padding: 20px 30px;
}
.informations .periodicity ul {
  color: hsl(236deg, 100%, 87%);
  display: flex;
  justify-content: space-between;
}
.informations .periodicity ul li {
  cursor: pointer;
  font-size: 1.8rem;
}
.informations .periodicity ul .active {
  color: white;
}
@media screen and (min-width: 900px) {
  .informations .periodicity ul {
    flex-direction: column;
  }
  .informations .periodicity ul li {
    margin: 7px 0;
  }
}

.card {
  margin-top: 22px;
  border-radius: 15px;
  color: white;
}
@media screen and (min-width: 900px) {
  .card {
    margin: 0;
  }
}
.card .header {
  height: 40px;
  position: relative;
  overflow: hidden;
  margin: 0 20px;
}
.card .header img {
  position: absolute;
  top: -9px;
  right: 0;
  height: 85px;
}
.card .stats {
  background-color: hsl(235deg, 46%, 20%);
  border-radius: 15px;
  padding: 20px 20px;
}
.card .stats div {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 7px 0;
}
.card .stats .title {
  font-size: 2rem;
  font-weight: 500;
}
.card .stats .title img {
  height: 5px;
  cursor: pointer;
}
.card .stats .hours p:nth-child(1) {
  font-size: 2.7rem;
  font-weight: 400;
}
.card .stats .hours p:nth-child(2) {
  font-size: 1.6rem;
  font-weight: 300;
  color: hsl(236deg, 100%, 87%);
}
.card .stats:hover {
  background-color: hsl(236deg, 42%, 30%);
}
@media screen and (min-width: 900px) {
  .card .stats .hours {
    flex-direction: column;
    align-items: flex-start;
  }
  .card .stats .hours p:nth-child(1) {
    font-size: 4.5rem;
    margin-bottom: 10px;
    font-weight: 300;
  }
}

.work {
  background-color: hsl(15deg, 100%, 70%);
}
@media screen and (min-width: 900px) {
  .work {
    grid-area: 1/2/2/3;
  }
}

.play {
  background-color: hsl(195deg, 74%, 62%);
}
@media screen and (min-width: 900px) {
  .play {
    grid-area: 2/2/3/3;
  }
}

.study {
  background-color: hsl(348deg, 100%, 68%);
}
@media screen and (min-width: 900px) {
  .study {
    grid-area: 1/3/2/4;
  }
}

.exercise {
  background-color: hsl(145deg, 58%, 55%);
}
@media screen and (min-width: 900px) {
  .exercise {
    grid-area: 2/3/3/4;
  }
}

.social {
  background-color: hsl(264deg, 64%, 52%);
}
@media screen and (min-width: 900px) {
  .social {
    grid-area: 1/4/2/5;
  }
}

.self-care {
  background-color: hsl(43deg, 84%, 65%);
}
@media screen and (min-width: 900px) {
  .self-care {
    grid-area: 2/4/3/5;
  }
}

.attribution {
  font-size: 11px;
  text-align: center;
}

.attribution a {
  color: hsl(228deg, 45%, 44%);
}

